import Vue from 'vue'
import Router from 'vue-router'
import Hotel from '@/components/hotel'
import HotelB from '@/components/hotelB'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hotel',
      component: Hotel
    },
    {
      path: '/B',
      name: 'HotelB',
      component: HotelB
    }
  ]
})



